.wrapper {
  max-width: 1200px !important;
}

@media only screen and (min-width: 600px) and (max-width: 900px) {
  .wrapper--match-content {
    padding-left: 30px !important;
    padding-right: 30px !important;
  }
}

.o-mega-menu__content-2-list--featured ul {
  margin-bottom: 1.875em;
}

.ccdb-search {
  width: 100vw !important;
  max-width: 100vw;
  margin-left: -50vw;
  margin-right: -50vw;
  box-sizing: border-box;
  left: 50%;
  right: 50%;
  position: relative;
  padding: 1.875rem;
  padding-bottom: 0;
}
@media only all and (max-width: 56.25em) {
  .ccdb-search {
    padding: 0.9375rem;
    padding-bottom: 0;
  }
}
.ccdb-search .o-expandable__header .o-expandable__cue-close,
.ccdb-search .o-expandable__header .o-expandable__cue-open {
  display: block !important;
}
.ccdb-search .o-expandable__label {
  margin-bottom: 0.83333333em !important;
}
.ccdb-search .cfpb-chart[data-chart-type=tile_map] .highcharts-legend__tile-map {
  transform: translate(7px, 50px);
}
.ccdb-search .cfpb-chart {
  max-width: 100% !important;
}
.ccdb-search #tile-chart-map {
  height: auto;
}
.ccdb-search .m-tags {
  padding-left: 0;
}